	module.exports = {"data":{"markdownRemark":{"html":"<p>I’ve been using Trello to manage the development process for my new project.  It is an absolutely fantastic tool for managing any type of small scale project.  It keeps things simple and helps you see the big picture. Its deeply flexible workflow consists of a hierarchy of boards, lists, cards and notes/checklists.  You can have multiple team members  sharing a board, with different cards assigned to different members.</p>\n<p>My personal setup has been to use a board to hold a new project, with lists for backlog (To Do), doing, and complete.  The cards hold feature level tasks, and I fill in the details for those tasks into the details section.  I’ve had no need for the card checklists yet, but those could be useful for more complicated features that don’t easily break down into subfeatures I suppose.  This has been great for staying organized.</p>\n<p>Trello is fast and intuitive, with surprising depth for a simple looking concept. It has apps for iOS, android and windows 8, as well as its web app.  I highly recommend it for anyone who has to organize any type of project involving 10 or fewer people.</p>","frontmatter":{"title":"Awesome Software: Trello","keywords":null,"category":"reviews","date":"2012-12-20T12:00:00+00:00","path":"/2012/12/20/awesome-software-trello","layout":"post","hideFooter":null},"fields":{"slug":"/2012/12/20/awesome-software-trello"}}},"pathContext":{"slug":"/2012/12/20/awesome-software-trello","relatedPosts":[{"path":"/2012/12/20/awesome-software-trello","data":{"title":"Awesome Software: Trello","path":"/2012/12/20/awesome-software-trello","description":"A quick review of the project management app Trello","date":"2012-12-20T12:00:00+00:00"}},{"path":"/2014/11/04/a-quick-review-of-google-inbox","data":{"title":"A Quick Review Of Google Inbox","path":"/2014/11/04/a-quick-review-of-google-inbox","description":"A product review of Google's new email UI","date":"2014-11-04T03:45:30+00:00"}},{"path":"/2014/07/09/understanding-the-backbone-mindset-a-review-of-building-backbone-plugins-by-derick-bailey","data":{"title":"Understanding the Backbone Mindset: A review of Building  Backbone Plugins by Derick Bailey","path":"/2014/07/09/understanding-the-backbone-mindset-a-review-of-building-backbone-plugins-by-derick-bailey","description":"A book review of a great deep dive into Backbone best practices","date":"2014-07-09T11:38:40+00:00"}}]}}

